interface Product {
  id: string
  name: string
  description: string
  price: number
  stock: number
  category: string
  salonId?: string
  imageUrl?: string
  status: "active" | "draft"
}

interface Booking {
  id: string
  clientName: string
  clientEmail: string
  service: string
  salonName: string
  date: string
  time: string
  status: "pending" | "confirmed" | "completed" | "cancelled"
  price: number
}

interface SystemLog {
  id: string
  type: "info" | "warning" | "error" | "success"
  message: string
  user: string
  timestamp: string
  details?: string
}

