  const [products, setProducts] = useState<Product[]>(mockProducts)
  const [bookings, setBookings] = useState<Booking[]>(mockBookings)
  const [logs, setLogs] = useState<SystemLog[]>(mockLogs)

  const [searchTerm, setSearchTerm] = useState("")
