  const [activeTab, setActiveTab] = useState("overview")

  const [newUser, setNewUser] = useState({ username: "", email: "", password: "", role: "gerant" as UserRole })
  const [newSalon, setNewSalon] = useState({ name: "", address: "", city: "", telephone: "", email: "", managerId: "" })
  const [newProduct, setNewProduct] = useState({ name: "", description: "", price: 0, stock: 0, category: "Soins" })
  const [editingUser, setEditingUser] = useState<User | null>(null)
  const [editingSalon, setEditingSalon] = useState<Salon | null>(null)
  const [editingProduct, setEditingProduct] = useState<Product | null>(null)

  const [userFilter, setUserFilter] = useState<string>("all")
  const [bookingFilter, setBookingFilter] = useState<string>("all")
  const [logFilter, setLogFilter] = useState<string>("all")

  useEffect(() => {
    if (!isAuthenticated || user?.role !== "superadmin") {
      router.push("/auth/login")
    }
  }, [isAuthenticated, user, router])

const token = useAuthStore((s) => s.token);

const handleAddUser = async () => {
  try {
     if (!token) {
      toast({ title: "Erreur", description: "Token manquant" });
      return;
    }

    const res = await fetch("http://localhost:3500/api/admin/create-user", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`, // très important !
      },
      body: JSON.stringify({
        username: newUser.username,
        email: newUser.email,
        password: newUser.password,
        role: newUser.role,
        telephone: newUser.telephone || null,
      }),
    });

    const data = await res.json();

    if (!res.ok) {
      toast({ title: "Erreur", description: data.message });
      return;
    }

    // Ajouter l'utilisateur retourné par l'API
    setUsers((prev) => [...prev, data.user]);

    toast({ title: "Succès", description: "Utilisateur ajouté avec succès" });

    setNewUser({ username: "", email: "", password: "", role: "gerant" });

  } catch (error) {
    console.error(error);
    toast({ title: "Erreur", description: "Erreur lors de l'ajout" });
  }
};




  const handleEditUser = (user: User) => {
    setEditingUser(user)
  }

  const handleUpdateUser = () => {
    if (!editingUser) return
    setUsers(users.map((u) => (u._id === editingUser._id ? editingUser : u)))
    toast({ title: "Succès", description: "Utilisateur modifié avec succès" })
    setEditingUser(null)
  }

  const handleDeleteUser = (id: string) => {
    if (users.find((u) => u._id === id)?.role === "superadmin") {
      toast({ title: "Erreur", description: "Impossible de supprimer un superadmin", variant: "destructive" })
      return
    }
    setUsers(users.filter((u) => u._id !== id))
    toast({ title: "Supprimé", description: "Utilisateur supprimé" })
  }

